.fixed {
    position: sticky;
    top: 0;
    z-index: 100;
  }
  .header__container {
    background-color: #1a1a1a;
    display: flex;
    padding: 0 20px;
    gap: 10px;
    align-items: center;
    justify-content: center;
  
    color: white;
    height: 70px;
  }
  .logo__container {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 200px;
  }
  .logo__container img {
    width: 90%;
    padding-top: 5px;
    vertical-align: middle;
  }
  .delivery {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3px;
    font-weight: bold;
    padding: 5px;
  }
  .delivery p {
    font-size: 10px;
  }
  .delivery span{
    font-size: 14px;
  }
  .search {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    overflow: hidden;
    width: 50%;
    flex: 1;
  }
  
  .search select {
    background-color: #e6e6e6;
  }
  
  .search select,
  .search input {
    padding: 10px;
    font-weight: bold;
    outline: 1px solid white;
  }
  .search select {
    border: none;
  }
  
  .search input {
    border: 1px solid white;
    width: 100%;
    margin-right: -2px;
    /* border: none; */
  }
  .search svg {
    padding: 10px;
    background: #febd69;
    color: #1a1a1a;
  }
  .search svg:hover {
    background-color: #d49644;
    cursor: pointer;
  }
  .search:focus-within {
    border: 2px solid rgb(235, 161, 173);
  }
  .order__container {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 450px;
    gap: 10px;
  }
  .order__container a {
    text-decoration: none;
    color: white;
  }
  .order__container p {
    font-size: 13px;
  }
  .language {
    width: 100px;
    display: flex;
    padding: 10px;
  }
  .language select {
    background-color: #1a1a1a;
    border: none;
    color: white;
  }
  .language img {
    width: 40%;
  }
  
  .cart {
    position: relative;
  }
  .cart span {
    position: absolute;
    font-weight: bold;
    top: -1px;
    font-size: 18px;
    background-color: #1a1a1a;
    height: 20px;
    padding: 2px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* border: 1px solid yellow; */
    left: 14px;
    color: orange;
  }
  .order__container a:hover,
  .delivery:hover,
  .logo__container a:hover,
  .lower__container ul li:hover {
    border: 1px solid white;
    border-radius: 3px;
  }
  
  .lower__container {
    background-color: #232f3e;
    padding-left: 20px;
    color: white;
  }
  .lower__container ul {
    display: flex;
    list-style: none;
    gap: 5px;
  }
  .lower__container ul li {
    padding: 8px;
  }
  .lower__container ul li:first-child {
    display: flex;
    gap: 2px;
    align-items: center;
  }
  
  .lower__container ul li:hover {
    cursor: pointer;
  }
  
  @media screen and (max-width: 576px) {
    .header__container {
      display: flex;
      flex-direction: column !important;
      height: auto;
    }
    .lower__container ul li:not(:first-child) {
      display: none;
    }
    .language {
      display: none;
    }
  }
  